import { useState, useEffect, useRef, useContext, use } from "react";
import toast from "react-hot-toast";
import { Link } from "react-router-dom";
import Layout from 'layouts'
import './Mint.scss'
import { Container, Image, Row, Col, Card, ProgressBar } from 'react-bootstrap'
import { useMediaQuery } from '@uidotdev/usehooks'
import { useConnectModal } from "@rainbow-me/rainbowkit";
import { useChainId, useAccount, useBalance, useReadContract, useWriteContract } from "wagmi";
import { ethers } from "ethers";
import { zeroAddress } from "viem";
import 'react-tabs/style/react-tabs.scss'
import {
  TEST_NET,
  NEW_GOTCHA_ADDRESS_TEST,
  NEW_GOTCHA_ADDRESS_MAIN,
  GG_ADDRESS_TEST,
  GG_ADDRESS_MAIN,
} from "../config";
import NewGotchaAbi from "../config/abis/NEW_GOTCHA_ABI.json";
import GGAbi from "../config/abis/GG_ABI.json";
import { RefreshContext } from '../context/RefreshContext'
import MintPopup from '../components/MintPopup/MintPopup';

// Import NFT images



const UINT256_MAX = "115792089237316195423570985008687907853269984665640564039457584007913129639935"

const useRefresh = () => {
  const { fast, slow } = useContext(RefreshContext)
  return { fastRefresh: fast, slowRefresh: slow }
}

export default function MintPage() {
  const isMobile = useMediaQuery("only screen and (max-width: 768px)");
  const { fastRefresh } = useRefresh();

  const { data: txResult, writeContractAsync } = useWriteContract();

  const { openConnectModal } = useConnectModal();
  const chainId = useChainId();
  const { isConnected, address } = useAccount();

  /////////////////////////////////////////////////////////////
  const { data: balance, refetch: refetchBalance } = useReadContract({
    address: chainId === TEST_NET ? GG_ADDRESS_TEST : GG_ADDRESS_MAIN,
    abi: GGAbi,
    functionName: "balanceOf",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  });

  const { data: allowance, refetch: refetchAllowance } = useReadContract({
    address: chainId === TEST_NET ? GG_ADDRESS_TEST : GG_ADDRESS_MAIN,
    abi: GGAbi,
    functionName: "allowance",
    args: [isConnected ? address : zeroAddress, chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN],
    chainId: chainId,
  });

  /////////////////////////////////////////////////////////////
  const { data: totalSupply, refetch: refetchTotalSupply } = useReadContract({
    address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
    abi: NewGotchaAbi,
    functionName: "totalSupply",
    args: [],
    chainId: chainId,
  });

  const { data: MAX_NFT_SUPPLY } = useReadContract({
    address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
    abi: NewGotchaAbi,
    functionName: "MAX_NFT_SUPPLY",
    enabled: true,
    args: [],
    chainId: chainId,
  });

  const { data: mintPrice, refetch: refetchMintPrice } = useReadContract({
    address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
    abi: NewGotchaAbi,
    functionName: "mintPrice",
    args: [],
    chainId: chainId,
  });

  const { data: mintedCnt, refetch: refetchMintedCnt } = useReadContract({
    address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
    abi: NewGotchaAbi,
    functionName: "mintCnt",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  });

  const { data: mintPause, refetch: refetchMintPause } = useReadContract({
    address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
    abi: NewGotchaAbi,
    functionName: "mintPause",
    args: [],
    chainId: chainId,
  });

  const [mintCnt, setMintCnt] = useState(1);
  const [mintCntAvailable, setMintCntAvailable] = useState(10);
  const [isPendingMint, setIsPendingMint] = useState(false);
  const [isApproving, setIsApproving] = useState(false);
  const [isNeedApprove, setIsNeedApprove] = useState(false);
  const [showMintPopup, setShowMintPopup] = useState(false);
  const [mintStatus, setMintStatus] = useState(''); // 'approving', 'minting', 'success', 'error'
  const [mintError, setMintError] = useState('');
  const [mintTxHash, setMintTxHash] = useState('');
  const [mintedTokenId, setMintedTokenId] = useState(null);

  useEffect(() => {
    refetchBalance();
    refetchAllowance();

    refetchTotalSupply();
    refetchMintPrice();
    refetchMintPause();
    refetchMintedCnt();
  }, [fastRefresh])

  useEffect(() => {
    if (allowance === undefined || balance === undefined || mintPrice === undefined) return;

    if (allowance >= mintPrice * ethers.toBigInt(mintCnt)) {
      setIsNeedApprove(false);
    } else {
      setIsNeedApprove(true);
    }
  }, [allowance, mintCnt])

  const increaseAmount = () => {
    if (mintCnt >= mintCntAvailable)
      setMintCnt(mintCntAvailable)
    else
      setMintCnt(mintCnt + 1)
  }

  const decreaseAmount = () => {
    if (mintCnt <= 1)
      setMintCnt(1)
    else
      setMintCnt(mintCnt - 1)
  }

  function getByText() {
    if (isApproving)
      return "Approving...";

    if (isPendingMint)
      return "Minting...";

    if (isNeedApprove)
      return "Approve";

    return "Mint";
  }

  async function handleMintByETH() {
    if (mintPause) {
      toast.error("Mint paused!. Please return later.");
      return;
    }

    if (totalSupply === MAX_NFT_SUPPLY) {
      toast.error("Already Minted out!");
      return;
    }

    if (mintCnt > mintCntAvailable || mintCntAvailable === 0) {
      toast.error(`Exceed mint limit. Available mint count is ${mintCntAvailable}.`);
      return;
    }

    if (mintCnt <= 0) {
      toast.error("Please input mint count.");
      return;
    }

    if (balance < mintPrice * ethers.toBigInt(mintCnt)) {
      toast.error("Your balance is not enough.");
      return;
    }

    if (isNeedApprove) {
      setIsApproving(true);
      setShowMintPopup(true);
      setMintStatus('approving');

      try {
        const tx = await writeContractAsync({
          address: chainId === TEST_NET ? GG_ADDRESS_TEST : GG_ADDRESS_MAIN,
          abi: GGAbi,
          functionName: "approve",
          args: [chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN, UINT256_MAX],
        });
        setMintTxHash(tx.hash);
        setIsApproving(false);
      } catch (err) {
        console.log(err);
        setIsApproving(false);
        setMintStatus('error');
        setMintError(err.message || 'Failed to approve tokens');
        return;
      }
    }

    setIsPendingMint(true);
    setMintStatus('minting');
    setShowMintPopup(true);

    try {
      const tx = await writeContractAsync({
        address: chainId === TEST_NET ? NEW_GOTCHA_ADDRESS_TEST : NEW_GOTCHA_ADDRESS_MAIN,
        abi: NewGotchaAbi,
        functionName: "mintNFTByToken",
        args: [ethers.toBigInt(mintCnt)]
      });
      
      setMintTxHash(tx.hash);
      // You might want to wait for the transaction receipt to get the token ID
      // const receipt = await tx.wait();
      // Parse event logs to get minted token ID if available
      setMintStatus('success');
      setIsPendingMint(false);
    } catch (err) {
      console.log(err);
      setIsPendingMint(false);
      setMintStatus('error');
      setMintError(err.message || 'Failed to mint NFT');
    }
  }

  // NFT showcase data


  // Calculate progress percentage
  const mintProgress = totalSupply && MAX_NFT_SUPPLY ? (Number(totalSupply) / Number(MAX_NFT_SUPPLY)) * 100 : 0;

  return (
    <Layout>
      {
        isConnected ?
          <>
            <Container fluid>
              <div className="mint-banner-container">
                <div className="mint-banner-wrapper">
                  <div className="mint-banner-image">
                    <img src={require("../assets/mission/banner3.jpeg")} alt="Mint Banner" />
                    <div className="mint-overlay"></div>
                    <div className="mint-badge">NEW GOTCHA COLLECTION</div>
                  </div>
                  <div className="mint-banner-content">
                    <h2 className="mint-title">ELEMENTAL GUARDIANS</h2>
                   
                    <p className="mint-description">Discover and collect legendary elemental guardians. Each NFT holds unique powers from the four realms: Fire, Water, Earth, and Air. Mint your guardian and unlock their mystical abilities.</p>
                    <div className="mint-features">
                      <div className="feature-item">
                        <div className="feature-icon">
                          <img src={require("../assets/icons/fire.png")} alt="Fire" />
                        </div>
                        <span className="feature-label">FIRE REALM</span>
                      </div>
                      <div className="feature-item">
                        <div className="feature-icon">
                          <img src={require("../assets/icons/water.png")} alt="Water" />
                        </div>
                        <span className="feature-label">WATER REALM</span>
                      </div>
                      <div className="feature-item">
                        <div className="feature-icon">
                          <img src={require("../assets/icons/earth.png")} alt="Earth" />
                        </div>
                        <span className="feature-label">EARTH REALM</span>
                      </div>
                      <div className="feature-item">
                        <div className="feature-icon">
                          <img src={require("../assets/icons/air.png")} alt="Air" />
                        </div>
                        <span className="feature-label">AIR REALM</span>
                      </div>
                    </div>
                  </div>
                  
                  {/* Integrated Mint Section */}
                  <div className="integrated-mint-section">
                    <Row className="justify-content-center">
                      <Col lg={6} md={8} sm={10}>
                        <div className="mint-card">
                        
                          
                          <div className="card-body">
                            <div className="mint-progress">
                              <div className="progress-text">
                                <span>Total Minted</span>
                                <span className="mint-count">{Number(totalSupply)} / {Number(MAX_NFT_SUPPLY)}</span>
                              </div>
                              <div className="custom-progress">
                                <div 
                                  className="progress-bar" 
                                  style={{ width: `${mintProgress}%` }}
                                >
                                  <div className="progress-glow"></div>
                                </div>
                              </div>
                            </div>
                            
                            <div className="mint-info">
                              <div className="info-row">
                                <span className="info-label">Your Minted NFTs</span>
                                <span className="info-value">{Number(mintedCnt)}</span>
                              </div>
                              <div className="info-row">
                                <span className="info-label">
                                  <span className="label-text">Mint Price</span>
                               
                                </span>
                                <span className="info-value">{mintPrice ? parseInt(ethers.formatEther(mintPrice)) : 0} $GG</span>
                              </div>
                              <div className="info-row">
                                <span className="info-label">
                                  <span className="label-text">Your $GG Balance</span>
                                 
                                </span>
                                <span className="info-value">{balance ? parseInt(ethers.formatEther(balance)) : 0} $GG</span>
                              </div>
                            </div>
                            
                            <div className="mint-controls">
                              <div className="amount-control">
                                <button
                                  onClick={decreaseAmount}
                                  className="amount-btn decrease"
                                  disabled={mintCnt <= 1}
                                >
                                  <span>-</span>
                                </button>

                                <div className="amount-input-container">
                                  <input
                                    type="text"
                                    readOnly
                                    value={mintCnt}
                                    className="amount-input"
                                  />
                                </div>

                                <button
                                  onClick={increaseAmount}
                                  className="amount-btn increase"
                                  disabled={mintCnt >= mintCntAvailable}
                                >
                                  <span>+</span>
                                </button>
                              </div>
                              
                              <button
                                onClick={handleMintByETH}
                                disabled={isPendingMint}
                                className="mint-button"
                              >
                                <span className="button-text">{getByText()}</span>
                                <div className="button-glow"></div>
                              </button>
                            </div>
                          </div>
                        </div>
                      </Col>
                    </Row>
                  </div>
                </div>
              </div>
              
              {/* Mission Container */}
              <div className="mission-earn-container">
                <div className="mission-earn-wrapper">
                  <div className="mission-earn-image">
                    <img src={require("../assets/mission/banner.jpeg")} alt="Mission Banner" />
                    <div className="mission-overlay"></div>
                    
                  </div>
                  <div className="mission-earn-content">
                    <h2 className="mission-earn-title">$GG WAITING FOR YOU TO EARN</h2>
                    <p className="mission-earn-description">Complete daily missions, feed your guardians. Your elemental guardians are ready for adventure!</p>
                  
                    <div className="mission-cta">
                      <Link to="/mission" className="mission-cta-btn">
                        <span className="btn-text">Start Earning</span>
                        <div className="btn-shine"></div>
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </Container>
          </>
          :
          <>
            <Container fluid>
              <div className="mint-banner-container">
                <div className="mint-banner-wrapper">
                  <div className="mint-banner-image">
                    <img src={require("../assets/mission/banner.jpeg")} alt="Connect Banner" />
                    <div className="mint-overlay"></div>
                    <div className="mint-badge">NEW GOTCHA COLLECTION</div>
                  </div>
                  <div className="mint-banner-content">
                    <h2 className="mint-title">ELEMENTAL GUARDIANS</h2>
                    <p className="mint-description">Connect your wallet to mint exclusive elemental NFTs with unique powers and abilities from the four realms: Fire, Water, Earth, and Air.</p>
                    
                    <div className="connect-section">
                      <button onClick={openConnectModal} className="custom-connect-btn">
                        <div className="btn-content">
                          <span className="btn-icon">ðŸ”—</span>
                          <span className="btn-text">Connect Wallet</span>
                        </div>
                        <div className="btn-shine"></div>
                      </button>
                    </div>
                  </div>
                  
          
                </div>
              </div>
              
              {/* Mission Container */}
             
               
           
            </Container>
          </>
      }
      <MintPopup
        show={showMintPopup}
        onHide={() => {
          setShowMintPopup(false);
          setMintStatus('');
          setMintError('');
          setMintTxHash('');
          setMintedTokenId(null);
        }}
        status={mintStatus}
        errorMessage={mintError}
        txHash={mintTxHash}
        mintedTokenId={mintedTokenId}
      />
    </Layout>
  )
}
