import React from 'react';
import './MintPopup.scss';

const MintPopup = ({ 
  show, 
  onHide, 
  status, // 'approving', 'minting', 'success', 'error'
  errorMessage,
  txHash,
  mintedTokenId
}) => {
  if (!show) return null;

  return (
    <div className="mint-popup-overlay">
      <div className="mint-popup-container">
        <div className={`mint-popup-content ${status}`}>
          <div className="popup-logo">
            <img src={require("../../assets/images/logoNew.png")} alt="Logo" />
          </div>

          {/* Loading Animation */}
          {(status === 'approving' || status === 'minting') && (
            <>
              <div className="loading-animation">
                <div className="circle"></div>
                <div className="circle"></div>
                <div className="circle"></div>
              </div>
              <h3>{status === 'approving' ? 'Approving Tokens' : 'Summoning Guardian'}</h3>
              <p className="status-message">
                {status === 'approving' 
                  ? 'Please confirm the transaction in your wallet'
                  : 'Preparing your Elemental Guardian for battle...'}
              </p>
            </>
          )}

          {/* Success State */}
          {status === 'success' && (
            <div className="success-content">
              <div className="success-animation">
                <svg className="checkmark" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 52 52">
                  <circle className="checkmark-circle" cx="26" cy="26" r="25" fill="none"/>
                  <path className="checkmark-check" fill="none" d="M14.1 27.2l7.1 7.2 16.7-16.8"/>
                </svg>
              </div>
              <h3>Guardian Summoned!</h3>
              <div className="success-details">
                {mintedTokenId && (
                  <div className="token-display">
                    <span className="label">Guardian ID:</span>
                    <span className="value">#{mintedTokenId}</span>
                  </div>
                )}
              </div>
              <button className="close-button" onClick={onHide}>Done</button>
            </div>
          )}

          {/* Error State */}
          {status === 'error' && (
            <div className="error-content">
              <div className="error-icon">
                <span>!</span>
              </div>
              <h3>Minting Failed</h3>
              <button className="close-button" onClick={onHide}>Close</button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default MintPopup; 